import React, { useState, useEffect } from 'react';
import styles from './index.css';
import { ModalStore, Store } from '../store';
// server方法
import { signInLottery } from '../service';
// 端内方法
// import Grounds from 'ThreeTerminal_base/grounds';
// 弹窗
import Toast from '../Toast';
// classNames库
import classNames from 'classnames/bind';
const cx = classNames.bind(styles);

const SignIn = () => {
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const { signlist, signdays, isGrounds, isLogin } = states;
    // 浮层
    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;

    // 日期格式化：YYYYMMDD
    const dateFormat = () => {
        const nowDate = new Date();
        const year = nowDate.getFullYear();
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();

        if (month < 10) month = `0${ month}`;
        if (day < 10) day = `0${ day}`;
        
        return year + month + day;
    };
    // 字符串转日期：YYYYMMDD => Date
    const dateStringToDate = (dateString) => {
        try {
          const year = dateString.substring(0, 4);
          const month = dateString.substring(4, 6);
          const day = dateString.substring(6, 8);
          let date = new Date(year, month - 1, day);
          const offset = date.getTimezoneOffset();

          date = new Date(date.getTime() - (offset * 60 * 1000));
                
            return date;
        } catch (error) {
            return null;
        }
      };
    // 判断是否是当日
    const getToday = (date) => {
        // 获取当日时间戳
        // const todayTimeStamp = (new Date(new Date().setHours(0, 0, 0, 0)) / 1000).toString();   
        // 获取当日日期
        const today = dateFormat(); // YYYYMMDD

        if (today === date) { 
            // console.log('今天');
            return true; 
        } else {
            // console.log('不是今天');
            return false; 
        }

    };

    // 判断单日签到状态
    const getState = (timeStampStr, signIn) => {
        // 获取当日时间戳
        // const todayTimeStamp = (new Date(new Date().setHours(0, 0, 0, 0)) / 1000).toString();   
        // 获取当日日期
        const todayStr = dateFormat(); // YYYYMMDD 
        // 0:失败断开 / 1:成功 / 2:未到日期
        let state = ''; 

        // console.log(timeStampStr === todayStr, timeStampStr, todayStr, signIn);

        if (signIn === '1') {
            return state = '已签到';
        } else if (timeStampStr < todayStr) {
            return state = '断开';
        } else {
            return state = '未到日期';
        }
    
    };

    // 对象转换成数组对象
    const objToArr = (obj) => {

        const arr = [];

        // 未签到
        if (window.grounds) {
            const isLogin = window.grounds.userIsLogin();

            if (isLogin) {
                for (const i in obj) {
                    const newObj = {
                        'timeStamp': i, // 时间戳
                        'signIn': obj[i], // 0 未签到 / 1 已签到
                        'state': getState(i, obj[i]), // 0 失败断开 / 1成功 / 2 未到日期
                        'isToday': getToday(i)
                    };
                    
                    // newObj[i] = obj[i];
                    arr.push(newObj);
                }
            } else {
                for (const i in obj) {
                    const newObj = {
                        'timeStamp': i, // 时间戳
                        'signIn': obj[i], // 0 未签到 / 1 已签到
                        'state': '未到日期', // 0 失败断开 / 1成功 / 2 未到日期
                        'isToday': getToday(i)
                    };
                    
                    // newObj[i] = obj[i];
                    arr.push(newObj);
                }
            }
        } else {
            for (const i in obj) {
                const newObj = {
                    'timeStamp': i, // 时间戳
                    'signIn': obj[i], // 0 未签到 / 1 已签到
                    'state': '未到日期', // 0 失败断开 / 1成功 / 2 未到日期
                    'isToday': getToday(i)
                };
                
                // newObj[i] = obj[i];
                arr.push(newObj);
            }

        }
        
return arr;
    };

    // 首次渲染签到UI数据
    useEffect(() => {
        if (window.grounds) {
            window.grounds.maximization();

            const isGrounds = true;
            const isLogin = window.grounds.userIsLogin();
    
            if (isLogin) {
                signInLottery()
                .then(res => {
    
                    const { code, msg, data } = res;
    
                    console.log('签到数据', res);
                    if (code !== 200) {
                        console.error(msg);
                    }
          
                    const { 
                        drawtime, // 剩余抽奖次数
                        signlist, // 签到列表
                        adddrawtime, // 本次签到增加抽奖天数
                        signdays, // 连续签到天数
                        signret // 签到结果：1 成功，2已经签到过
                    } = data;
    
                    // 未签到
                    if (window.grounds) {
                        const isLogin = window.grounds.userIsLogin();
                        
                        if (signret === 1 && isLogin) {
                            Toast.default({ content: `今日签到成功，抽奖次数+${adddrawtime}` });
                            console.log('今日签到成功');
                        } else {
                            console.log('已经签到过');
                        }
                    }
    
                    // 数据储存
                    if (signlist) {
                        changeStates({ drawtime, signlist: objToArr(signlist), signdays });
                    }
    
                })
                .catch(e => {
                    console.error(e);
                });
            } else { // 未登录
                    const defualt = {
                        '20290111': '0',
                        '20290112': '0',
                        '20290113': '0',
                        '20290114': '0',
                        '20290117': '0',
                        '20290118': '0',
                        '20290119': '0'
                    };
                
                    // 数据储存
                     changeStates({ drawtime: 0, signlist: objToArr(defualt), signdays: 2 });
              
            }
        } else { // 端外
            const defualt = {
                '20290111': '0',
                '20290112': '0',
                '20290113': '0',
                '20290114': '0',
                '20290117': '0',
                '20290118': '0',
                '20290119': '0'
            };
        
            // 数据储存
             changeStates({ drawtime: 0, signlist: objToArr(defualt), signdays: 2 });
        }

    }, []);

    const handleRuleBtnClick = () => {
        changeModalStates({ signs: true });
    };
    
return (
        <div className={styles.wrap}>
            <div className={styles.content}>

                <div className={styles.header}>
                    <div className={styles.sign}>已连续签到<span className={styles.day}>{!isLogin ? 0 : signdays}</span>天</div> 
                    <div className={styles.rule} onClick={handleRuleBtnClick}>
                            <div className={styles.ruleText}>签到规则</div>
                            <div className={styles.ruleIcon} />
                    </div>
                </div>
                
            <div className={styles.listWrap}>
            <div className={styles.list}>
                {signlist.map((item, index) => {
                  return (
                      <div
                          className={styles.itemWrap}
                          key={index}>

                              <div className={styles.cardWrap}>
                                    <div
                                        className={cx({
                                            itemIcon: true,
                                            signIcon: item.state === '已签到', /* 已签到 */
                                            noSignIcon: item.state === '断开', /* 断开 */
                                            noDateIcon: item.state === '未到日期'/* 未到日期 */
                                    })} 
                                    />

                                    <div
                                    
                                        className={cx({
                                            textList: true,
                                            signText: item.state === '已签到', /* 已签到 */
                                            noSignText: item.state === '断开', /* 断开 */
                                            noDateText: item.state === '未到日期'/* 未到日期 */
                                    })} >
                                        {item.isToday ? '今天' : `第${index + 1}天`}
                                    </div>
                              </div>

                            <div
                                className={cx({
                                    itemBg: true,
                                    signedBg: item.state === '已签到', /* 已签到 */
                                    noSignBg: item.state === '断开', /* 断开 */
                                    noDateBg: item.state === '未到日期'/* 未到日期 */
                            })} />

                      </div>
                  );
                })}
            </div> 
            </div>
            </div>

        </div>
    );
};

export default SignIn;
