import React, { useEffect, useRef, useState } from 'react';
import styles from './index.css';
import { ModalStore } from '../../store';
import { useModalOpen } from '../hooks';

const Signs = () => {
    const scrollRef = useRef(null);
    const [scrollBtnHeigt, setScrollBtnHeigt] = useState(0);
    const [scrollBtnTop, setScrollBtnTop] = useState(0);

    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;

    useEffect(() => {
        if (scrollRef.current) {
            const scrollHeight = scrollRef.current.scrollHeight;
            const textHeight = scrollRef.current.offsetHeight;

            setScrollBtnHeigt((textHeight / scrollHeight) * 100);
        }
    }, []);

    const handleScroll = () => {
        if (scrollRef.current) {
            const scrollHeight = scrollRef.current.scrollHeight;
            const scrollTop = scrollRef.current.scrollTop;

            setScrollBtnTop((scrollTop / scrollHeight) * 100);
        }
    };

    const handleClose = () => {
        changeModalStates({ signs: false });
    };

    const handlePreventClose = e => {
        e.stopPropagation();
    };

    useModalOpen();

    return (
        <div className={styles.mask} onClick={handleClose}>
            <div className={styles.modal} onClick={handlePreventClose}>
                <div className={styles.content}>
                    <div className={styles.textWrap}>
                        <div className={styles.textScroll} ref={scrollRef} onScroll={handleScroll}>
                            <div className={styles.text}>
                                <div className={styles.section}>
                                    {/* <strong>活动时间：</strong>  */}
     
                                    打开凤凰新闻APP - “我”，找到“新
                                    <br /> 
                                    年惊喜”可进入活动页面进行签到。
                                    <br /> 
                                    每日签到即可获得一次抽奖机会，连
                                    <br /> 
                                    续签到三天可额外获得一次抽奖机
                                    <br /> 
                                    会，连续签到五天可额外获得两次抽
                                    <br /> 
                                    奖机会。活动持续七天，单一用户最
                                    <br /> 
                                    多可累计获得15次抽奖机会。断签则
                                    <br />
                                    重新计算。
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* <div className={styles.scrollBar}>
                        <div
                            className={styles.scrollBtn}
                            style={{ height: `${scrollBtnHeigt}%`, top: `${scrollBtnTop}%` }}
                        />
                    </div> */}
                </div>
                <div className={styles.closeBtn} onClick={handleClose} />
            </div>
        </div>
    );
};

export default Signs;
